.i {
    display: flex;
    height: 100vh;
}

.i-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right {
    flex: 1;
    position: relative;
}

.i-left-wrapper {
    padding: 50px;
    height: 50%;
    /* background-color: #2b1a52; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro {
    font-size: 30px;
    font-weight: 300;
}

.i-name {
    font-size: 60px;
}

.i-title {
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    50% {
        transform: translateY(-50px);
    }
    100% {
        transform: translateY(-100px);
    }
}

.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #59b256;
    /* background-color: #603fbb; */
    display: flex;
    align-items: center;
}

.i-scroll {
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}

.i-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    position: absolute;
}

.i-bg {
    clip-path: polygon(75% 0%, 100% 50%, 70% 83%, 0 100%, 0 31%, 24% 5%);
    background-color: #8bd42b;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    right: 0;
}